@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto:wght@300&display=swap');


.opiate {
    width: 100%;
    height: 70vh;
    background: #0000007a;
    position: relative;
  }
  
  .opiate:before {
    content: '';
    position: absolute;
    background: url('https://rodeo-recovery-substance-abuse.s3.us-west-1.amazonaws.com/opiate.jpg') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    z-index: -1;
  }
  

.opiate {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.opiate .opiate-content {
    display: block;
    position: absolute;
    bottom: 20%;
    padding: 1rem;
}

.opiate h1 {
    font-size: 3rem;
    font-family: 'Montserrat', sans-serif;
    color: rgba(255, 255, 255, 0.692);
}


/* MEDIA QUERIES */

@media screen and (max-width:940px) {

.opiate h1 {
    font-size: 2rem;
}

}